import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class Nav extends Component {
  render() {
    return (
      <div>
        <nav className="blue darken-3">
          <div className="nav-wrapper">
            <a href="/reservations" className="brand-logo center">Tee Time Maker</a>
            <a data-activates="main-menu" className="button-collapse show-on-large">
              <i className="fa fa-bars"></i>
              </a>
            <ul className="right hide-on-small-only">
              <li><Link to="/reservations"><i className="fa fa-clock-o"></i> Reservations</Link></li>         
            </ul>
            <ul className="side-nav" id="main-menu">
            <li><Link to="/reservations"><i className="fa fa-clock-o"></i> Reservations</Link></li>  
            <li><Link to="/reservations/add"><i className="fa fa-plus"></i> Add Reservation</Link></li>  
            <li><Link to="/about"><i className="fa fa-question-circle"></i> About</Link></li> 
            </ul>
          </div>
        </nav>

      </div>
    )
  }
}

export default Nav;


