import React, {Component} from 'react';
import {Button, Table, Input} from 'react-materialize';
import '../App.css';

class PlayerPicker extends Component {

  constructor(props) {
    super(props);

    this.handleRemove = this
      .handleRemove
      .bind(this);

      this.showModal = this
      .showModal
      .bind(this);

    this.state = {
      show: false,

      players: [
        {
          name: "Kirsten Boulia",
          id: "1234"
        }
      ],
      buddies: [
        {
          name: "Lauren Boulia",
          id: "5678"
        }, {
          name: "Ryder Boulia",
          id: "9012"
        }
      ]
    };

  }

  showModal = () => {
    this.setState({ show: true });
  }
  
  hideModal = () => {
    this.setState({ show: false });
  }

  handleRemove(event) {

    // initially mark the element to be removed from the list
    let value = event.target.value;

    console.log("changed! " + value);

  }

  render() {
    console.log("PlayerPicker: render");

    let handleRemove = this.handleRemove;
    let showModal = this.showModal;
    let players = this.state.players;
    let emptySlots = [];

    if (players.length < 3) {
      let i;

      for (i = 0; i < (3 - players.length); i++) {
        emptySlots.push({name: "Available", id: ""});
      }
    }

    return (
      <div>

        <Modal show={this.state.show} handleClose={this.hideModal} >
          <p>Modal</p>
          <p>Data</p>
        </Modal>

        <Table>
          <thead>
            <tr>
              <th data-field="pos"></th>
              <th data-field="name">Players</th>
              <th data-field="addremove"></th>
            </tr>
          </thead>

          <tbody>

            <tr>
              <td>1</td>
              <td>
                <Input s={12} defaultValue="You" disabled/>
              </td>
              <td></td>
            </tr>

            {players
              .map(function (player, index) {

                return <tr key={player.id}>
                  <td>{index + 2}</td>
                  <td>
                    <Input s={12} defaultValue={player.name} disabled/>
                  </td>
                  <td>
                    <Button onClick={showModal}>
                      Change...
                    </Button>
                  </td>
                </tr>

              })}

            {emptySlots
              .map(function (player, index) {

                return <tr key={index}>
                  <td>{index + 2}</td>
                  <td>
                    <Input name='group1' type='select'>
                      <option value="0">Available</option>
                      <option value="1">Kirsten Boulia</option>
                      <option value="2">Lauren Boulia</option>
                      <option value="3">Ryder Boulia</option>
                    </Input>
                  </td>
                  <td></td>
                </tr>

              })}

          </tbody>
        </Table>

      </div>
    )
  }
}

const Modal = ({ handleClose, show, children }) => {
  const showHideClassName = show ? 'modal display-block' : 'modal display-none';

  return (
    <div className={showHideClassName}>
      <section className='modal-main'>
        {children}
        <Button
          onClick={handleClose}
        >
          Close
        </Button>
      </section>
    </div>
  );
};

export default PlayerPicker;


